"use client";

import Link from "next/link";
import { useEffect, useState, useRef } from "react";

function useInView<T extends Element>(opts?: IntersectionObserverInit) {
  const [inView, setInView] = useState(false);
  const ref = useRef<T | null>(null);

  useEffect(() => {
    const el = ref.current;
    if (!el) return;

    const io = new IntersectionObserver(([entry]) => {
      if (entry.isIntersecting) setInView(true);
    }, opts ?? { threshold: 0.15 });

    io.observe(el);
    return () => io.disconnect();
  }, [opts]);

  return { ref, inView };
}

function Reveal({
  children,
  delayMs = 0,
  y = 20,
}: {
  children: React.ReactNode;
  delayMs?: number;
  y?: number;
}) {
  const { ref, inView } = useInView<HTMLDivElement>({ threshold: 0.15 });

  return (
    <div
      ref={ref}
      style={{
        transitionDelay: `${delayMs}ms`,
        transform: inView ? "translateY(0px)" : `translateY(${y}px)`,
        opacity: inView ? 1 : 0,
      }}
      className="transition-[transform,opacity] duration-700 ease-out will-change-transform"
    >
      {children}
    </div>
  );
}

function InteractiveCard({ 
  number, 
  title, 
  description, 
  details, 
  index 
}: { 
  number: string; 
  title: string; 
  description: string; 
  details: string[]; 
  index: number;
}) {
  const [isHovered, setIsHovered] = useState(false);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const cardRef = useRef<HTMLDivElement>(null);

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!cardRef.current) return;
    const rect = cardRef.current.getBoundingClientRect();
    setMousePosition({
      x: e.clientX - rect.left,
      y: e.clientY - rect.top,
    });
  };

  return (
    <div
      ref={cardRef}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onMouseMove={handleMouseMove}
      className="group relative cursor-pointer"
    >
      {/* Holographic background effect */}
      <div
        className="absolute inset-0 rounded-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"
        style={{
          background: `radial-gradient(600px circle at ${mousePosition.x}px ${mousePosition.y}px, rgba(0, 0, 0, 0.15), transparent 40%)`,
          filter: 'blur(40px)',
        }}
      />

      {/* Glass morphism card */}
      <div
        className="relative rounded-3xl border border-neutral-200/50 bg-white/80 backdrop-blur-xl p-8 lg:p-10 transition-all duration-500"
        style={{
          transform: isHovered 
            ? `perspective(1000px) rotateX(${((mousePosition.y - 200) / 20) * -1}deg) rotateY(${(mousePosition.x - 300) / 20}deg) translateZ(20px) scale(1.02)` 
            : 'perspective(1000px) rotateX(0) rotateY(0) translateZ(0) scale(1)',
          boxShadow: isHovered 
            ? '0 20px 60px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.05), inset 0 1px 0 rgba(255, 255, 255, 0.9)' 
            : '0 4px 20px rgba(0, 0, 0, 0.04), 0 0 0 1px rgba(0, 0, 0, 0.02)',
        }}
      >
        {/* Animated border gradient */}
        <div
          className="absolute inset-0 rounded-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 pointer-events-none"
          style={{
            background: 'linear-gradient(135deg, rgba(0, 0, 0, 0.1) 0%, transparent 50%, rgba(0, 0, 0, 0.1) 100%)',
            mask: 'linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)',
            maskComposite: 'exclude',
            padding: '1px',
          }}
        />

        {/* Number with futuristic effect */}
        <div className="flex items-start gap-6 mb-6">
          <div className="relative">
            <span 
              className="text-xs font-semibold font-mono tracking-[0.12em] text-neutral-400 transition-all duration-500 group-hover:text-neutral-900"
              style={{
                textShadow: isHovered ? '0 0 20px rgba(0, 0, 0, 0.3)' : 'none',
              }}
            >
              {number}
            </span>
            <div 
              className="absolute -left-2 -top-2 w-8 h-8 rounded-full bg-neutral-900/5 blur-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500"
            />
          </div>
          <h2 
            className="text-3xl sm:text-4xl lg:text-5xl font-bold leading-[1.15] tracking-[-0.025em] font-[var(--font-general-sans)] text-neutral-900 transition-all duration-500"
            style={{
              transform: isHovered ? 'translateX(4px)' : 'translateX(0)',
            }}
          >
            {title}
          </h2>
        </div>

        {/* Description */}
        <p className="text-lg sm:text-xl leading-[1.75] font-normal text-neutral-800 max-w-2xl mb-8 transition-colors duration-300 group-hover:text-neutral-900">
          {description}
        </p>

        {/* Interactive details grid */}
        <div className="grid sm:grid-cols-2 gap-4">
          {details.map((detail, detailIndex) => (
            <div
              key={detailIndex}
              className="flex items-start gap-3 p-3 rounded-xl bg-neutral-50/50 backdrop-blur-sm transition-all duration-300 group-hover:bg-neutral-100/50"
              style={{
                transform: isHovered ? `translateY(${detailIndex % 2 === 0 ? '-2px' : '2px'})` : 'translateY(0)',
                transitionDelay: `${detailIndex * 50}ms`,
              }}
            >
              <div className="flex-shrink-0 w-5 h-5 rounded-full bg-neutral-900/10 flex items-center justify-center mt-0.5 group-hover:bg-neutral-900 transition-colors duration-300">
                <span className="text-[10px] font-semibold text-neutral-600 group-hover:text-white transition-colors duration-300">✓</span>
              </div>
              <p className="text-sm leading-relaxed text-neutral-700 group-hover:text-neutral-900 transition-colors duration-300">
                {detail}
              </p>
            </div>
          ))}
        </div>

        {/* Scan line effect on hover */}
        <div
          className="absolute inset-0 rounded-3xl opacity-0 group-hover:opacity-100 pointer-events-none overflow-hidden"
          style={{
            background: 'linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.03) 50%, transparent 100%)',
            animation: isHovered ? 'scan 2s ease-in-out infinite' : 'none',
          }}
        />
      </div>

      <style jsx>{`
        @keyframes scan {
          0% { transform: translateY(-100%); }
          100% { transform: translateY(100%); }
        }
      `}</style>
    </div>
  );
}

